.block-team {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-team__inner {
  padding: 110px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 45px;
}
.block-team__inner .block-title {
  max-width: 475px;
  color: rgb(var(--primary-color));
  text-align: center;
}
.block-team__inner .block-title span,
.block-team__inner .block-title strong {
  color: rgb(var(--secondary-color));
}
.block-team .team-members {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-team .team-members__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 15px;
}
.block-team .team-members__wrapper .member {
  flex: 1 0 25%;
  padding: 0 15px;
}
.block-team .team-members .member {
  min-width: 280px;
  padding: 0;
  display: flex;
  justify-content: center;
}
.block-team .team-members .member:hover .member__photo::before {
  transform: translate(-50%, -50%) scale(1) rotate(180deg);
}
.block-team .team-members .member:hover .member__photo::after {
  height: 100%;
}
.block-team .team-members .member:hover .member__details::before {
  height: 4px;
  background-color: rgb(var(--primary-color));
}
.block-team .team-members .member:hover .member__details::after {
  height: 100%;
}
.block-team .team-members .member:hover .member__details .name {
  color: #fff;
}
.block-team .team-members .member:hover .member__details .social-networks svg {
  fill: #fff;
}
.block-team .team-members .member:hover .member__details .position {
  color: rgb(var(--primary-color));
}
.block-team .team-members .member__inner {
  display: flex;
  flex-direction: column;
}
.block-team .team-members .member__photo {
  width: 100%;
  height: 318px;
  position: relative;
  display: flex;
}
.block-team .team-members .member__photo::before {
  content: "";
  background: url(./plus.png) no-repeat;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  z-index: 2;
  transition: all 0.3s linear;
}
.block-team .team-members .member__photo::after {
  content: "";
  width: 100%;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  background-color: rgba(var(--primary-color), 0.3);
  transition: all 0.3s linear;
}
.block-team .team-members .member__photo img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}
.block-team .team-members .member__details {
  padding: 40px 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}
.block-team .team-members .member__details::before {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  display: block;
  background-color: rgb(var(--light-grey));
  transition: all 0.3s linear;
}
.block-team .team-members .member__details::after {
  content: "";
  width: 100%;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  display: block;
  background-color: rgb(var(--secondary-color));
  transition: all 0.3s linear;
}
.block-team .team-members .member__details .name {
  margin-bottom: 0;
  position: relative;
  z-index: 1;
  background-color: transparent;
  font-size: 24px;
  font-weight: 600;
  color: rgb(var(--primary-color));
  text-align: center;
  transition: all 0.3s linear;
}
.block-team .team-members .member__details .position {
  margin-bottom: 0;
  position: relative;
  z-index: 1;
  background-color: transparent;
  font-size: 16px;
  font-weight: 400;
  font-family: var(--roboto);
  color: rgb(var(--secondary-color));
  text-align: center;
  transition: all 0.3s linear;
}
.block-team .team-members .member__details .social-networks {
  position: relative;
  z-index: 1;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.block-team .team-members .member__details .social-networks li {
  width: 34px;
  height: 34px;
  flex: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-team .team-members .member__details .social-networks li:hover svg {
  opacity: 0.8;
}
.block-team .team-members .member__details .social-networks li a {
  width: 100%;
  height: 100%;
  padding: 9px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-team .team-members .member__details .social-networks li svg {
  width: 16px;
  height: 16px;
  display: block;
  fill: rgb(var(--light-grey));
  transition: all 0.3s linear;
}